import React from 'react';

class FrontEnd extends React.Component{

	componentDidMount() {
		window.scrollTo(0,0);
   	document.title = "Codificao Front-End, PSD para HTML, web components e muito mais";
  }
	
	render(){
		return(
			<section className="page-services">
				<div className="banner-single banner-front">
					<div className="banner banner-fe"></div>
					<h1 className="banner-title">Front-End</h1>
				</div>
				<div className="page">
					<h1 className="title">Front End</h1>
					<h3>Transformando layouts em cdigos</h3>
				</div>
				<div className="content">
					<div className="description page">
						<p>
							Servio voltado para designers freelancer e agncias. Profissionais 
							que produzem todo o layout, mas precisam de uma mozinha na hora da 
							codificao.
						</p><br/>
						<p>
							O servio de codificao de layouts (front-end)  oferecido  partir 
							de um layout que j foi criado. Seja ele feito em Photoshop, Fireworks, 
							Illustrator ou em qualquer outro software grfico que voc utilize. 
							Esse layout  fatiado e transformado em cdigo HTML 5, CSS 3 e JavaScript, 
							e sendo necessrio, utilizo de bibliotecas e frameworks como Bootstrap, Angular ou React, 
							a fim de produzir um cdigo	mais robusto.
						</p><br/>
						<p>
							Tudo isso sem deixar de seguir as orientaes e especificaes tcnicas 
							desenvolvidas pela <a href="http://www.w3c.br/Home/WebHome">W3C</a>.
						</p>
					</div>
				</div>
				
			</section>
		)
	}
}

export default FrontEnd;


