import React from 'react';

class Site extends React.Component{

  componentDidMount() {
    window.scrollTo(0,0);
    document.title = "Criao de sites dinmicos, performticos, responsivos e que funcionam em qualquer device (smartphones, tablets e smartTVs)";
  }
    
	render(){
		
		return(
			<section className="page-services">

				<div className="banner-single banner-site">
					<div className="banner banner-st"></div>
					<h1 className="banner-title">Sites</h1>
				</div>

				<div className="page">
					<h1 className="title">Criao de Sites</h1>
					<h3 className="subtitle">Apresente seu negcio para o mundo!</h3>
				</div>	
				
				<div className="content">
					<div className="description page">
						<p>
							As vantagens em se ter um site so inmeras, entre elas, 
							te possibilita uma comunicao direta e eficiente com seus 
							potenciais clientes, eles podem acessar informaes detalhadas 
							de seus produtos e servios, ver fotos, vdeos, depoimentos de 
							clientes satisfeitos e outros detalhes que por fim os ajudam a 
							tomar decises favorveis ao seu negcio.

						</p><br/>
						<p>
							Sabendo-se que cada vez mais o primeiro contato entre o cliente e 
							a empresa  feito pelo site, ter uma presena web marcante tem que 
							fazer parte da estratgia da empresa e o desenvolvimento de um site 
							institucional faz parte desse processo.
						</p><br/>
						<p>
							A construo do site tem como foco o usurio a fim de proporcionar 
							uma experincia nica para o usurio. Com uma navegao simples e 
							com poucos cliques, o cliente encontra o que procura em seu site, 
							aumentando assim o seu ndice de converso.
						</p>
					</div>
				</div>
			</section>
		)	
	}
}

export default Site;


